.box {
    background-color: rgb(1, 4, 9);

    .slogan-box {
        background-image: url('https://frank-li-files.oss-cn-beijing.aliyuncs.com/study_center/slogan_bg.jpg');
        background-repeat: no-repeat;
        background-position: center;
        background-clip: border-box;
        background-size: cover;

        .slogan {
            font-size: 3rem;
            font-weight: 700;
        }

        .desc {
            font-size: 1.5rem;
            color: #ccc;
        }
    }

    .sections {
        .section {
            .logo {
                height: 4rem;
                width: auto;
            }
        }
    }

}